.errorText {
    color: red;
}

.loginButton {
    padding: 10px 20px;
    border: 1px solid gray;
    border-radius: 5px;
    margin: 0 5px;
    background-color: white;
}

.loginButton:hover {
    background-color: #67BA80;
    transition: .1s;
}

.loginButton:active {
    transition: 0s;
    background-color: white;
}